.navbar__container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 9% 7%;
}

.title {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;
  font-size: 70px;
  font-weight: bold;
  line-height: 90%;
  margin-bottom: 60px;
}

.navBar__button {
  width: 100%;
  height: 90px;
  margin-bottom: 40px;
  background: #2AC0C4;
  border: 2px solid #BEBEBE;
  font-size: 32px;
  color: white;    
}

.navBar__button:hover {
  cursor: pointer;
}



